export default {
	data() {
		return {
			backgroundColor:['green','yellow','orange','darkgray','#D8BFD8','#E6E6FA','	#483D8B','#9932CC'],
			colorType:0
		};
	},
	props: {
		content: {
			type: String,
			default: '轻松一校'
		},
	},
	methods: {
		changeColorType() {
			this.colorType = Math.floor(Math.random()*this.backgroundColor.length)
		}
	},
	created(){
		this.changeColorType()
	}
}
